import React, { useState } from 'react';
import {
    Button,
    IconButton,
    Tooltip,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    CircularProgress,
    Alert,
    Typography,
    Box,
    Divider,
    Paper
} from '@mui/material';
import axios from '../api/axios';
import { toast } from 'react-toastify';
import UndoIcon from '@mui/icons-material/Undo';
import RentalForm from './RentalForm';
import AddIcon from "@mui/icons-material/Add";

const VehicleActions = ({ vehicle, refreshData }) => {
    const [showRentalForm, setShowRentalForm] = useState(false);
    const [showReturnDialog, setShowReturnDialog] = useState(false);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [invoice, setInvoice] = useState(null);

    const handleReturnClick = () => {
        setShowReturnDialog(true);
        setInvoice(null);
        setError(null);
    };

    const handleReturnConfirm = async () => {
        setLoading(true);
        setError(null);

        try {
            const response = await axios.post(
                `/api/vehicles/${vehicle.id}/return`,
                {},
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                }
            );

            setInvoice(response.data);
            toast.success('Ara baaryla teslim alnd');
        } catch (err) {
            console.error('Return Error:', err); // ekleyin
            const errorMessage = err.response?.data?.message || err.message || 'Ara teslim alma ilemi baarsz oldu';
            setError(errorMessage);
            toast.error(errorMessage);
        } finally {
            setLoading(false);
        }
    };

    const handleCloseReturnDialog = () => {
        setShowReturnDialog(false);
        if (invoice && typeof refreshData === 'function') {
            refreshData();
        }
    };

    return (
        <>
            {vehicle.status === 'AVAILABLE' ? (
                <Tooltip title="Arac Kirala">
                    <IconButton
                        color="primary"
                        onClick={() => setShowRentalForm(true)}
                    >
                        <AddIcon />
                    </IconButton>
                </Tooltip>
            ) : vehicle.status === 'RENTED' ? (
                <Tooltip title="Teslim Al">
                    <IconButton
                        color="error"
                        onClick={handleReturnClick}
                    >
                        <UndoIcon />
                    </IconButton>
                </Tooltip>
            ) : null}

            {showRentalForm && (
                <RentalForm
                    vehicle={vehicle}
                    onClose={(success) => {
                        setShowRentalForm(false);
                        if (success) {
                            toast.success('Kiralama ilemi baarl');
                            refreshData();
                        }
                    }}
                />
            )}

            <Dialog open={showReturnDialog} maxWidth="md" fullWidth onClose={handleCloseReturnDialog}>
                <DialogTitle>{vehicle.brand} {vehicle.model} - Ara Teslim Alma</DialogTitle>

                {error && <Alert severity="error" sx={{ mx: 3, mb: 2 }}>{error}</Alert>}

                {!invoice ? (
                    <DialogContent>
                        <Typography variant="body1" paragraph>
                            {vehicle.plate} plakal arac teslim almak istediinize emin misiniz?
                        </Typography>
                        <Alert severity="info">
                            Planlanan teslim tarihinden sonra teslim alnrsa, ge teslim cezas uygulanacaktr.
                        </Alert>
                    </DialogContent>
                ) : (
                    <DialogContent>
                        <Paper elevation={3} sx={{ p: 3 }}>
                            <Typography variant="h6" gutterBottom>Fatura Bilgileri</Typography>
                            <Typography variant="subtitle2">Fatura No: {invoice.invoiceNumber}</Typography>
                            <Typography variant="subtitle2">Tarih: {new Date(invoice.issueDate).toLocaleDateString()}</Typography>

                            <Divider sx={{ my: 2 }} />

                            <Box sx={{ mb: 2 }}>
                                <Typography variant="body2">Ara: {invoice.vehiclePlate}</Typography>
                                <Typography variant="body2">Gnlk Kiralama creti: {vehicle.dailyPrice} TL</Typography>
                                <Typography variant="body2">Kiralama Sresi: {invoice.rentalDays} gn</Typography>
                            </Box>


                            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                <Typography variant="body2">Kiralama Tutar:</Typography>
                                <Typography variant="body2">{invoice.baseAmount} TL</Typography>
                            </Box>
                            {invoice.lateFee > 0 && (
                                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                    <Typography variant="body2" color="error">
                                        Aklama:
                                    </Typography>
                                    <Typography variant="body2" color="error">{invoice.lateFeeDescription} </Typography>
                                </Box>
                            )}

                            {invoice.lateFee > 0 && (
                                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                    <Typography variant="body2" color="error">
                                        Ge Teslim Cezas:
                                    </Typography>
                                    <Typography variant="body2" color="error">{invoice.lateFee} TL</Typography>
                                </Box>
                            )}

                            <Divider sx={{ my: 2 }} />

                            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                                <Typography variant="subtitle1" fontWeight="bold">TOPLAM:</Typography>
                                <Typography variant="subtitle1" fontWeight="bold">{invoice.totalAmount} TL</Typography>
                            </Box>
                        </Paper>
                    </DialogContent>
                )}

                <DialogActions>
                    {!invoice ? (
                        <>
                            <Button onClick={handleCloseReturnDialog}>ptal</Button>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={handleReturnConfirm}
                                disabled={loading}
                            >
                                {loading ? <CircularProgress size={24} /> : 'Teslim Al'}
                            </Button>
                        </>
                    ) : (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={handleCloseReturnDialog}
                        >
                            Kapat
                        </Button>
                    )}
                </DialogActions>
            </Dialog>
        </>
    );
};

export default VehicleActions;