import React, { useState, useEffect } from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    CircularProgress,
    Typography,
    Alert,
    IconButton,
    Tooltip, Chip
} from '@mui/material';
import HistoryIcon from '@mui/icons-material/History';
import axios from '../api/axios';
import DescriptionIcon from '@mui/icons-material/Description'; // Eksik import eklendi
import { toast } from 'react-toastify'; // Toast bildirimleri iin



const RentalHistoryModal = ({ vehicleId }) => {
    const [open, setOpen] = useState(false);
    const [rentals, setRentals] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const fetchRentalHistory = async () => {
        setLoading(true);
        setError(null);
        try {
            const response = await axios.get(`/api/vehicles/${vehicleId}/rentals`);
            setRentals(response.data);
        } catch (err) {
            setError(err.response?.data?.message || 'Kiralama gemii yklenemedi');
            console.error('Kiralama gemii hatas:', err);
        } finally {
            setLoading(false);
        }
    };
    const downloadContract = async (rentalId) => {
        try {
            const response = await axios.get(`/api/vehicles/${vehicleId}/contracts/${rentalId}`, {
                responseType: 'blob',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });

            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', `sozlesme-${rentalId}.docx`);
            document.body.appendChild(link);
            link.click();
            link.remove();
        } catch (error) {
            console.error('Szleme indirme hatas:', error);
            toast.error('Szleme indirilirken hata olutu');
        }
    };

    const handleOpen = () => {
        setOpen(true);
        fetchRentalHistory();
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <>
            <Tooltip title="Kiralama Gemii">
                <IconButton onClick={handleOpen} color="primary">
                    <HistoryIcon />
                </IconButton>
            </Tooltip>

            <Dialog open={open} onClose={handleClose} maxWidth="xl" fullWidth>
                <DialogTitle>Kiralama Gemii (Ara ID: {vehicleId})</DialogTitle>

                <DialogContent>
                    {loading ? (
                        <div style={{ display: 'flex', justifyContent: 'center', padding: '20px' }}>
                            <CircularProgress />
                        </div>
                    ) : error ? (
                        <Alert severity="error">{error}</Alert>
                    ) : rentals.length === 0 ? (
                        <Typography variant="body1">Bu araca ait kiralama kayd bulunamad</Typography>
                    ) : (
                        <TableContainer component={Paper}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Fatura No</TableCell>
                                        <TableCell>Mteri</TableCell>
                                        <TableCell>TC Kimlik</TableCell>
                                        <TableCell>Telefon</TableCell>
                                        <TableCell>Kiralama Sresi</TableCell>
                                        <TableCell>Balang</TableCell>
                                        <TableCell>Planlanan Biti</TableCell>
                                        <TableCell>Gerek Biti</TableCell>
                                        <TableCell>Kira Tutar</TableCell>
                                        <TableCell>Ge Teslim Cezas</TableCell>
                                        <TableCell>Toplam</TableCell>
                                        <TableCell>Durum</TableCell>
                                        <TableCell>Szleme</TableCell>                                        <TableCell>
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {rentals.map((rental) => (
                                        <TableRow key={rental.id}
                                                  sx={{
                                                      backgroundColor: rental.active ? '#f5f5f5' : 'inherit',
                                                      '&:hover': { backgroundColor: '#fafafa' }
                                                  }}
                                        >
                                            <TableCell>{rental.invoiceNumber || '-'}</TableCell>
                                            <TableCell>{rental.customerName}</TableCell>
                                            <TableCell>{rental.customerTC}</TableCell>
                                            <TableCell>{rental.customerPhone}</TableCell>
                                            <TableCell>{rental.duration}</TableCell>
                                            <TableCell>{rental.startDate}</TableCell>
                                            <TableCell>{rental.endDate}</TableCell>
                                            <TableCell>{rental.returnDate || '-'}</TableCell>
                                            <TableCell sx={{ fontWeight: 600 }}>{rental.formattedBaseAmount}</TableCell>
                                            <TableCell sx={{ color: rental.hasLateFee ? 'error.main' : 'inherit' }}>
                                                {rental.formattedLateFee}
                                            </TableCell>
                                            <TableCell sx={{ fontWeight: 800 }}>{rental.formattedTotalAmount}</TableCell>
                                            <TableCell>
                                                <Chip
                                                    label={rental.status}
                                                    color={
                                                        rental.status === 'Aktif' ? 'primary' :
                                                            rental.status === 'Tamamland' ? 'success' : 'warning'
                                                    }
                                                    size="small"
                                                />
                                            </TableCell>
                                            <TableCell>
                                                <Tooltip title="Szlemeyi ndir">
                                                    <IconButton
                                                        onClick={() => downloadContract(rental.id)}
                                                        color="primary"
                                                    >
                                                        <DescriptionIcon />
                                                    </IconButton>
                                                </Tooltip>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    )}
                </DialogContent>

                <DialogActions>
                    <Button onClick={handleClose}>Kapat</Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default RentalHistoryModal;